<template>
  <div class="storage">
    <div class="d-flex align-items-center">
      <p>{{data.name}}</p>
      <button class="btn-danger m-1 ml-3" @click="delStorage">Delete</button>
    </div>

    <coin-component v-for="coin in data.coins" :key="coin.id" :data="coin" :cmc="cmc" @update_storages="updStorages"></coin-component>

    <div class="coin" v-if="show_new">
      <div class="coin-data"><input type="text" v-model="new_coin.symbol" @keyup="checkCoin" maxlength="5" placeholder="Coin symbol"></div>
      <div class="coin-data"><input type="number" step="0.0001" v-model="new_coin.value" placeholder="How much"></div>
      <div class="coin-data"><span :class="is_coin" v-html="gCoin"></span></div>
      <button :class="saveBtnClass" @click="saveCoin" :disabled="!good_coin_name">Add!</button>
      <button class="btn-secondary" @click="show_new = !show_new">Cancel</button>
    </div>

    <div class="coin" v-else>
      <button class="btn-outline-info" @click="show_new = !show_new">Add coin</button>
    </div>

  </div>
</template>

<script>
import CoinComponent from './Coin.vue';


export default {
  props: ['data', 'cmc'],
  data: function() {
    return {
      storages: {},
      show_new: false,
      new_coin: {
        symbol: "",
        value: "",
        storage_id: this.data.id,
      },
      is_coin: "wrong",
      show_new: false,
      good_coin_name: false,
    }
  },
  computed: {
    saveBtnClass: function() {
      return this.good_coin_name == true ? "btn-success" : "btn-dark";
    },
    gCoin() {
      return  this.good_coin_name == true ? "&#9989;" : "&#10060;";
    },
  },
  methods: {
    checkCoin() {
      this.good_coin_name = this.new_coin.symbol in this.cmc ? true : false;
    },
    delStorage() {
      let conf = confirm('Del this coin?');
      if (!conf) {
        return
      }
      let token = JSON.parse(this.$cookies.get('token')).token_info;
      console.log(token.access_token);
      this.$http.delete(
        "http://caru.pythonanywhere.com/cry/api/v1/storages/",
        {headers: {"Authorization": "Bearer " + token.access_token},
        body: this.data}
      ).then(resp => {
        console.log(resp.data);
      }, e => {
        console.log("error", e.data);
      });
      this.updStorages();
    },
    saveCoin() {
      let token = JSON.parse(this.$cookies.get('token')).token_info;
      this.$http.post(
        "http://caru.pythonanywhere.com/cry/api/v1/coin/",
        this.new_coin,
        {headers: {"Authorization": "Bearer " + token.access_token,}}
      ).then(resp => {
        console.log(resp.data);
      }, e => {
        console.log(e.data);
      });
      this.show_new = !this.show_new;
      this.new_coin = {
        symbol: "",
        value: "",
        storage_id: this.data.id,
      };
      this.updStorages();
    },
    updStorages() {
      this.$emit('update_storages');
    }
  },
  components: {
    CoinComponent,
  }
}

</script>

<style>

</style>



