<template>
  <div id="app">
    <login-component v-if='state == "login"' @logined='onMain' :msg="'parent data'"></login-component>
    <main-component v-else-if='state == "main"' @logout='logOut' @refreshToken="refreshToken"></main-component>
  </div>
</template>

<script>
import LoginComponent from './components/Login.vue';
import MainComponent from './components/Main.vue';
export default {
  // name: 'app',
  data () {
    return {
      state: this.$cookies.isKey('token') ? 'main' : 'login',
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    onMain() {
      this.state = 'main';
    },
    logOut() {
      this.state = 'login';
    },
    refreshToken() {
      let grant_type = "refresh_token";
      let client_id = "i224V5TlTvNxP5yKBTZ3hf6oCtV3wweSWgFswK1i";
      let client_secret = "p9dAsh9gL88OP2nv28BjiOD0FHUXC22kz1lXB2FqOsPy6LYguyOKFi2atWdUmbHIANGcQ3LpVRqAWc3O4fxTAryRMHwLc45IU2L7WVoaDtWXPewYyYLEKbSNYlczHiSv";
      let refresh_token = JSON.parse(this.$cookies.get('token')).token_info.refresh_token;

      this.$http.post(
        'http://caru.pythonanywhere.com/cry/o/token/',
        `grant_type=${grant_type}&client_secret=${client_secret}&client_id=${client_id}&refresh_token=${refresh_token}`,
        {
          headers: {
            'content-type': 'application/x-www-form-urlencoded'
          },
      })
      .then(response => {
        let token = {
          created: new Date(),
          token_info: response.body
        };
        this.$cookies.remove('token');
        this.$cookies.set('token', JSON.stringify(token));
        this.loading = false;
        console.log(response.data);
      }, e => {
        console.log(e);
      });
      console.log("refreshed");
    }
  },
  components: {
    LoginComponent,
    MainComponent,
  }
}
</script>

<style>

body {
  min-width: 835px;
}
p{
  margin: 0;
}
button {
  cursor: pointer;
  background: #aaa;
  padding: 5px;
}

#app {
  min-height: 100vh;
  max-height: 100vw;
}
#login-form {
  margin-top: 20px;
}
#main {
  min-height: 100vh;
}
.head {
  display: flex;
  padding: 20px;
  justify-content: flex-end;
}
.content {
  display: flex;
}
aside {
  min-width: 20vw;
}
#storages {
}
.str-zone {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.total {
  display: flex;
}
.total p {
  margin: 10px;
}
.storage {
  border: 1px dashed #99D;
  padding: 5px;
  margin: 5px;
}
.add-storage {
  width: 100%;
  display: flex;
}
.coin {
  margin: 0 10px;
  padding: 2px 10px;
  display: flex;
  align-items: center;
}
.coin input {
  width: 70px;
}
.coin-data {
  padding-right: 30px;
  width: 150px;
  text-align: right;
}
.coin-data:first {
  text-align: left;
}
.coin-data input {
  box-sizing: border-box;
  width: 146px;
  margin: 0 2px;
}
.btns {
  margin-left: auto;
  width: 150px;
}




</style>



