<template>
  <div v-if="!edit" class="coin">
    <div class="coin-data">{{data.symbol}}</div>
    <div class="coin-data">{{data.value | toFloat | round(3, true) }}</div>
    <div class="coin-data">USD {{data.cost | round(2, true) }}</div>
    <div class="btns">
      <button class="btn-info" @click="edit = !edit">Edit</button>
      <button class="btn-danger" @click="delCoin">Delete</button>
    </div>
  </div>
  <div v-else class="coin">
    <div class="coin-data"><input type="text" v-model="new_coin.symbol" @keyup="checkCoinName" maxlength="5"></div>
    <div class="coin-data"><input type="number" step="0.0001" v-model="new_coin.value" @keyup="checkCoinName"></div>
    <div class="coin-data"><span :class="is_coin" v-html="gCoin"></span></div>
    <div class="btns">
      <button :class="saveBtnClass" @click="putCoin" :disabled="!good_coin_name">Save</button>
      <button class="btn-secondary" @click="cancelEdit">Cancel</button>
    </div>
  </div>
</template>

<script>

import round from 'vue-round-filter';


export default {
  props: ['data', 'cmc'],
  data: function() {
    return {
      new_coin: Object.assign({}, this.data),
      edit: false,
      good_coin_name: true,
      is_coin: "green"
    }
  },
  methods: {
    cancelEdit() {
      this.new_coin = Object.assign({}, this.data);
      this.edit = !this.edit;
    },
    checkCoinName() {
      this.good_coin_name = this.new_coin.symbol in this.cmc? true : false;
    },
    putCoin() {
      if (!this.good_coin_name) {
        console.log('wrong coin name')
        return;
      }
      let token = JSON.parse(this.$cookies.get('token')).token_info;
      this.$http.put(
        "http://caru.pythonanywhere.com/cry/api/v1/coin/",
        this.new_coin,
        {headers: {"Authorization": "Bearer " + token.access_token,}}
      ).then(resp => {
        console.log(resp.data);
      }, e => {
        console.log(e.data);
      });
      this.edit = !this.edit;
      this.$emit('update_storages');
    },
    delCoin() {
      let conf = confirm('Del this coin?');
      if (!conf) {
        return
      }
      let token = JSON.parse(this.$cookies.get('token')).token_info;
      console.log(token.access_token);
      this.$http.delete(
        "http://caru.pythonanywhere.com/cry/api/v1/coin/",
        {headers: {"Authorization": "Bearer " + token.access_token},
        body: this.data}
      ).then(resp => {
        console.log(resp.data);
      }, e => {
        console.log("erroer", e.data);
      });
      this.$emit('update_storages');
    }
  },
  filters: {
    round,
    toFloat(val) {
      return parseFloat(val);
    }
  },
  computed: {
    cost: function () {
      if ( this.data != null ) {
        return this.data.price * this.data.value;
      }
      return 0;
    },
    saveBtnClass: function() {
      return this.good_coin_name == true ? "btn-success" : "btn-dark";
    },
    gCoin() {
      return  this.good_coin_name == true ? "&#9989;" : "&#10060;";
    }
  }
}



</script>

<style>


</style>



