<template>
  <div id="main">
    <div class="head">
      <button id="logout" class="btn-primary" @click='logout'>LogOut</button>
    </div>
    <div class="content">
      <aside class="">
        <p class="text-uppercase text-center">
          <span class="btn btn-outline-info p-1 font-weight-bold">{{username}}</span>
        </p>
      </aside>
      <storages-component></storages-component>
    </div>
  </div>
</template>

<script>
import StoragesComponent from './Storages.vue';


export default {

  data: function() {
    return {
      storages: ""
    }
  },
  created() {
    let result = {};
    let token = JSON.parse(this.$cookies.get('token'));
    this.username = token.username;
    let now = new Date();
    let created = new Date(token.created);
    let gona_lose = (now - created) / 1000 - token.token_info.expires_in;
    result = gona_lose > 600;
    if (result) {
      // console.log(token, (now - created) / 1000);
      this.$emit('refreshToken');
    } else {
      console.log('tooken is allright');
    }
  },
  methods: {
    logout() {
      let token = JSON.parse(this.$cookies.get('token')).token_info.access_token;
      let client_id = "i224V5TlTvNxP5yKBTZ3hf6oCtV3wweSWgFswK1i";
      let client_secret = "p9dAsh9gL88OP2nv28BjiOD0FHUXC22kz1lXB2FqOsPy6LYguyOKFi2atWdUmbHIANGcQ3LpVRqAWc3O4fxTAryRMHwLc45IU2L7WVoaDtWXPewYyYLEKbSNYlczHiSv";

      this.$http.post(
        "http://caru.pythonanywhere.com/cry/o/revoke_token/",
        `token=${token}&client_id=${client_id}&client_secret=${client_secret}`,
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded"
          }
        }
      ).then(resp => {
        console.log("token was deleted from cookies and revoked from backend");
      }, e => {
        console.log(e);
      });
      this.$cookies.remove('token');
      this.$emit('logout');
    },
  },
  components: {
    StoragesComponent,
  }

}

</script>

<style>

</style>



