<template>
  <div id="storages">
    <div class="str-zone">
      <p class="m-0">Storages:</p>
      <button id="update" class="btn-info" @click="getStorages">Update</button>
    </div>
    <div class="total font-weight-bold">
      <p>Total:</p>
      <p>USD {{all | round(2,true)}}</p>
      <p>RUB {{all*62.5 | round(2,true)}}</p>
    </div>
    <div>
      <button class="btn-outline-info" @click="toogle_adding" v-if="!adding_storage">Add storage</button>
      <div v-else class="add-storage">
        <input type="text" v-model="storage_name" placeholder="Name of new storage">
        <button class="btn-success" @click="postNewStorage">Add</button>
        <button class="btn-secondary" @click="toogle_adding">Cancel</button>
      </div>
    </div>
    <storage-component v-for="st in storages" :key='st.id' :data='st' :cmc='cmc' @update_storages="getStorages"></storage-component>
  </div>
</template>

<script>
import StorageComponent from './Storage.vue';
import round from 'vue-round-filter';

export default {

  data: function() {
    return {
      storages: {},
      storage_name: "",
      adding_storage: false,
    }
  },
  computed: {
    all: function() {
      let result = 0;
      for (let storage in this.storages) {
        for (let coin in this.storages[storage].coins) {
          coin = this.storages[storage].coins[coin];
          result += coin.cost;
        }
      }
      return result;
    }
  },
  filters: {
    round,
  },
  created() {
    this.getCoinMarketCap();
  },
  methods: {
    getCoinMarketCap() {
      let cmc = {}
      this.$http.get('https://api.coinmarketcap.com/v2/ticker/')
      .then(request => {
        let data = request.body.data;
        for (let i in data) {
          let coin = data[i];
          let new_coin = {
            id: coin.id,
            name: coin.name,
            symbol: coin.symbol,
            price: coin.quotes.USD.price
          };
          cmc[coin.symbol.toLowerCase()] = new_coin
        }
        this.cmc = cmc;
        this.getStorages();
      }), e => {
        console.log(e);
      }
    },

    getStorages() {
      const wait = time => new Promise((resolve) => setTimeout(resolve, time));
      wait(500).then(() => {
        this.updStorages()
      });
    },

    updStorages() {
      let token = JSON.parse(this.$cookies.get('token')).token_info;
      this.$http.get(
        'http://caru.pythonanywhere.com/cry/api/v1/storages/',
        {headers: {"Authorization": "Bearer " + token.access_token,}})
      .then(request => {
        let result = request.body
        for (let st of result) {
          for (let coin of st.coins) {
            let price = this.cmc[coin.symbol].price;
            let volume = parseFloat(coin.value);
            let cost = price * volume;
            coin['price'] = price;
            coin['value'] = volume;
            coin['cost'] = cost;
          }
        }
        this.storages = result
      }), e => {
        console.log(e);
      };
    },

    postNewStorage() {
      if (this.storage_name == "") {
        alert("Input storage_name")
        return
      }
      let token = JSON.parse(this.$cookies.get('token')).token_info;
      this.$http.post(
        "http://caru.pythonanywhere.com/cry/api/v1/storages/",
        {'storage_name': this.storage_name},
        {headers: {"Authorization": "Bearer " + token.access_token,}}
      ).then(resp => {
        console.log(resp.data);
        this.getStorages();
      }, e => {
        console.log(e.data);
      });
      this.toogle_adding();
    },
    toogle_adding() {
      this.storage_name = "";
      this.adding_storage = !this.adding_storage;
    }
  },
  components: {
    StorageComponent,
  }
}

</script>

<style>


</style>



