<template>
  <div id="login-form" class="">
    <div class="center">
      <div class="zone">
        <label for="username">Name</label>
        <input type="text" id="username" v-model="user.username">
      </div>
      <div class="zone" v-if="registration">
        <label for="email">Email*</label>
        <input type="email" id="email" v-model="user.email">
      </div>
      <div class="zone">
        <label for="paswword">Password</label>
        <input type="password" id="password" v-model="user.password">
      </div>
      <div class="zone" v-if="registration">
        <label for="confirm">Password</label>
        <input id="confirm" type="password" v-model="user.confirm">
      </div>
      <div id="info" class="zone">
        <p>{{ info_text }}</p>
      </div>
      <div class="log-btns">
        <div v-if="!registration">
          <button @click.prevent="getToken" class="btn btn-info">Login</button>
          <button @click="startRegistration" class="btn btn-info">Register</button>
        </div>
        <div v-else>
          <button @click="regUser" class="btn btn-info">Registr</button>
          <button @click="cancelReg" class="btn btn-info">Cancel</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['msg'],
  data: function () {
    return {
      info_text: "Login pls",
      user: {
        username: "",
        email: "",
        password: "",
        confirm: "",
      },
      loading: false,
      registration: false,
    }
  },
  methods: {
    startRegistration() {
      this.registration = !this.registration;
      this.info_text = "Input data pls";
    },
    cancelReg() {
      this.registration = !this.registration;
      this.info_text = "Login pls";
    },
    getToken: function () {
      if (this.$cookies.isKey('token')) {
        this.$emit('logined');
        return;
      }
      this.loading = true;

      let grant_type = "password";
      let client_id = "i224V5TlTvNxP5yKBTZ3hf6oCtV3wweSWgFswK1i";
      let client_secret = "p9dAsh9gL88OP2nv28BjiOD0FHUXC22kz1lXB2FqOsPy6LYguyOKFi2atWdUmbHIANGcQ3LpVRqAWc3O4fxTAryRMHwLc45IU2L7WVoaDtWXPewYyYLEKbSNYlczHiSv";

      this.$http.post(
        'http://caru.pythonanywhere.com/cry/o/token/',
        `grant_type=${grant_type}&client_secret=${client_secret}&client_id=${client_id}&username=${this.user.username}&password=${this.user.password}`,
        {headers: {'content-type': 'application/x-www-form-urlencoded'},})
      .then(response => {
        let token = {
          created: new Date(),
          token_info: response.body,
          username: this.user.username
        }
        this.$cookies.set('token', JSON.stringify(token));
        this.loading = false;
        this.$emit('logined', );
      }, e => {
        this.info_text = "Wrang name or password!"
        console.log(e);
      });
      this.loading = false;
    },
    regUser() {
      if (this.user.password != this.user.confirm) {
        this.info_text = "Passwords are unequal!"
        console.log("wrong passwords", this.user.password, this.user.confirm);
        return;
      }
      this.loading = true;
      this.$http.post('http://127.0.0.1:8000/accounts/adduser/', this.user).then(resp => {
        this.registration = false;
        this.info_text = "Succss register. Pls input your name and password."
        console.log("registration was successful");
      }, e => {
        if ("username" in e.data) {
          this.info_text = `Name '${this.user.username}' already used!`;
        } else if ("email" in e.data) {
          this.info_text = `Wrong or already used email!`
        } else if ("password" in e.data) {
          this.info_text = `Wrong password or paswword hasn't at least 6 characters!`
        }
        console.log(e.data);
      });
    }
  },
}
</script>

<style scoped>

p {
  margin: 0;
}

#login-form{
  border: 1px solid #ada;
  width: 300px;
  height: 300px;
  margin: 30px auto;
  max-width: 100vw;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  border-radius: 20px;
}
.center {
  width: 250px;
  height: 250px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
}
.zone {
  display: flex;
  justify-content: space-between;
  padding: 2px;
}
#info {
  margin: auto;
  margin-bottom: 0;
}
.log-btns {
  margin-top: auto;
}
.log-btns div{
  display: flex;
  justify-content: space-around;
}
input {
  width: 150px;
  padding: 0 15px;
}

</style>



